.eb-gallery-img-wrapper {
  overflow: hidden;

  .eb-gallery-link-wrapper {
    overflow: hidden;
    display: block;
    position: relative;
    height: 100%;
    width: 100%;
  }
  .eb-gallery-img-content{
    position: relative;
    display: block;
    overflow: hidden;

    .eb-gallery-img-caption {
      position: absolute;
      width: auto;
      transform: translate(0%, 0%);
      font-size: .8em;
      line-height: 1em;
    }
  }

  &.grid {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-auto-flow: dense;

    .eb-gallery-img-content{

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  &.masonry {
    display: block;
    columns: 3;

    .eb-gallery-img-content{
      break-inside: avoid;
      display: block;

      img {
        display: block;
      }
    }
  }

  &.caption-on-hover {
    .eb-gallery-img-content {
      .eb-gallery-img-caption {
        opacity: 0;
        transition: all 0.4s ease-in-out;
  
        &.left.top {
          left: -10px;
        }
        &.left.middle {
          left: -10px;
        }
        &.left.bottom {
          left: -10px;
        }
        &.center.top {
          top: -10px
        }
        &.center.middle {
          bottom: 0;
          transform: translate(-50%, 20px);
        }
        &.center.bottom {
          bottom: -10px;
        }
        &.right.top {
          right: -10px;
        }
        &.right.middle {
          right: -10px;
        }
        &.right.bottom {
          right: -10px;
        }
      }
      &:hover .eb-gallery-img-caption {
        opacity: 1;
      }
    }
  }

  .eb-gallery-img-content,
  &.caption-on-hover .eb-gallery-img-content:hover {
    .eb-gallery-img-caption {
      &.left.top {
        left: 0px;
        top: 0px
      }
      &.left.middle {
        left: 0px;
        top: 50%;
        transform: translateY(-50%);
      }
      &.left.bottom {
        left: 0px;
        bottom: 0px
      }
      &.center.top {
        left: 50%;
        top: 0px;
        transform: translateX(-50%);
      }
      &.center.middle {
        left: 50%;
        top: 50%;
        bottom: auto !important;
        transform: translate(-50%,-50%);
      }
      &.center.bottom {
        left: 50%;
        bottom: 0px;
        transform: translateX(-50%);
      }
      &.right.top {
        right: 0px;
        top: 0px;
      }
      &.right.middle {
        right: 0px;
        top: 50%;
        transform: translateY(-50%);
      }
      &.right.bottom {
        right: 0px;
        bottom: 0px;
      }
    }
  }
}

// Sortable item styles
.eb-lightbox-sortable-item {
  display: flex;
  justify-content: space-between;
  border: 1px solid;
  line-height: 2.5em;
}

// Sortable style
.eb-lightbox-sortable-title {
  cursor: pointer;
  flex: 12;
  padding-left: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 200px;
  white-space: nowrap;
}

// Upload button style
.eb-gallery-upload-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 60px;
  width: 100%;
  background: #e3e3e3;
  color: #666666;
}

// Image close button style
.eb-lightbox-delete-icon {
  position: absolute;
  margin-left: 88%;
  margin-top: 1%;
  color: #f44336;
}

.eb-lightbox-image-wrapper {
  position: relative;
}

.eb-is-sorting {
  outline: 4px solid;
}

@media (min-width: 600px) {
  .columns-8 a {
    flex-basis: calc(12.5% - 16px);
  }
  .columns-7 a {
    flex-basis: calc(14.28% - 16px);
  }
  .columns-6 a {
    flex-basis: calc(16.667% - 16px);
  }
  .columns-5 a {
    flex-basis: calc(20% - 16px);
  }
  .columns-4 a {
    flex-basis: calc(25% - 16px);
  }
  .columns-3 a {
    flex-basis: calc(33.33% - 16px);
  }
  .columns-2 a {
    flex-basis: calc(50% - 16px);
  }
  .columns-1 a {
    flex-basis: 100%;
  }
}

.eb-gallery-img-link {
  position: relative;
  cursor: pointer;
}

.eb-lightbox-caption-input-wrapper {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  margin-top: 10px;
}

// Caption styles

// 1st style
.caption-style-1 {
  overflow: hidden;
}
.caption-style-1 .eb-gallery-img-caption {
  opacity: 0;
}

.caption-style-1 .eb-gallery-img {
  transition: all 0.25s ease-in-out;
  filter: grayscale(100%) !important;
}

.caption-style-1 .eb-gallery-img-content:hover .eb-gallery-img {
  filter: grayscale(0%) !important;
}

.caption-style-1 .eb-gallery-img-content:hover .eb-gallery-img-caption {
  opacity: 1;
  transition: all 0.3s ease-in-out;
}

// 2nd style
.eb-gallery-img-wrapper.caption-style-2 .eb-gallery-link-wrapper:after {
  content: '';
  position: absolute;
  margin: 0px !important;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease-in-out;
}

.eb-gallery-img-wrapper.caption-style-2 .eb-gallery-link-wrapper .eb-gallery-img-caption {
  z-index: 99;
  transition: all 0.4s ease-in-out;
  visibility: hidden;
}
.eb-gallery-img-wrapper.caption-style-2 .eb-gallery-link-wrapper:hover .eb-gallery-img-caption {
  visibility: visible;
}

.eb-gallery-img-wrapper.caption-style-2.overlay-top .eb-gallery-link-wrapper:after {
  top: 0;
  left: 0;
  width: 100%;
  height: 0;
}
.eb-gallery-img-wrapper.caption-style-2.overlay-top .eb-gallery-link-wrapper:hover:after {
  height: 100%;
}

.eb-gallery-img-wrapper.caption-style-2.overlay-bottom .eb-gallery-link-wrapper:after {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
}
.eb-gallery-img-wrapper.caption-style-2.overlay-bottom .eb-gallery-link-wrapper:hover:after {
  height: 100%;
}

.eb-gallery-img-wrapper.caption-style-2.overlay-left .eb-gallery-link-wrapper:after {
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
}
.eb-gallery-img-wrapper.caption-style-2.overlay-left .eb-gallery-link-wrapper:hover:after {
  width: 100%;
}

.eb-gallery-img-wrapper.caption-style-2.overlay-right .eb-gallery-link-wrapper:after {
  top: 0;
  right: 0;
  width: 0;
  height: 100%;
}
.eb-gallery-img-wrapper.caption-style-2.overlay-right .eb-gallery-link-wrapper:hover:after {
  width: 100%;
}

.eb-gallery-img-wrapper.caption-style-2.zoom .eb-gallery-link-wrapper:after {
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  transform: scale(0);
}
.eb-gallery-img-wrapper.caption-style-2.zoom .eb-gallery-link-wrapper:hover:after {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  transform: scale(1);
}

.caption-style-2 .eb-gallery-img {
  transition: all 0.25s ease-in-out;
}

